import React, {useState} from "react";
import {NavLink} from 'react-router-dom'

const Navbar =() =>{
  const[navLinkOpen,navLinkToggle]=useState(false);
  const handleNavLinksToggle=()=>{
    navLinkToggle(!navLinkOpen)
  }

  const renderClasses=() =>{
    let classes ="navlinks"
    if(navLinkOpen){
      classes +=" active"
    }
    return classes
  }
  return <nav>
  <div className="logo">
  <i className="fas fa-mountain"></i>
<h4>Steins Gate</h4>
</div>
<ul className={renderClasses()}>
<li className ="link"><a href="/">Home</a></li>
<li className ="link"><a href="/Characters">Lab Members</a></li>
<li className ="link"><a href="/WatchOrder">Watch Order</a></li>
<li className ="link"><a href="/ImageGallery">Image Gallery</a></li>
<li className ="link"><a href="/VideoClips">Video Clips</a></li>
</ul>
<div onClick={handleNavLinksToggle} className="hamburger-toggle">
<i className=" fas fa-bars fa-lg"></i>
</div>
</nav>;
}

export default Navbar
