import React from 'react';
import ReactDOM from "react-dom";
import Navbar from "./components/Navbar";
import "./App.css";
import Showcase from "./components/Showcase";
import Characters from "./components/Characters";
import {BrowserRouter as Router, Route , Switch} from 'react-router-dom';
import WatchOrder from "./components/WatchOrder";
import ImageGallery from "./components/ImageGallery";
import VideoClips from "./components/VideoClips";
import {Grid} from'@material-ui/core';


import Okabepg from './components/characters/Okabepg';
import Mayuripg from './components/characters/Mayuripg';
import Itarupg from './components/characters/Itarupg';
import Kurisupg from './components/characters/Kurisupg';
import Moekapg from './components/characters/Moekapg';
import Lukapg from './components/characters/Lukapg';
import Catpg from './components/characters/Catpg';
import Suzuhapg from './components/characters/Suzuhapg';
import Mahopg from './components/characters/Mahopg';
import Kagaripg from './components/characters/Kagaripg';
import Yukipg from './components/characters/Yukipg';



const App =() => {
  return (
    <Router>

    <div>
    <Navbar />
     <div className ="main">
       <Switch>
          <Route exact path="/">
            <Showcase />
          </Route>
          <Route path="/characters">
            <Characters />
          </Route>
          <Route path="/watchorder">
            <WatchOrder />
          </Route>
          <Route path="/imagegallery">
            <ImageGallery />
          </Route>
          <Route path="/videoclips">
            <VideoClips />
          </Route>
          <Route path="/Okabepg">
           <Okabepg />
          </Route>
          <Route path="/Mayuripg">
              <Mayuripg />
          </Route>
          <Route path="/Itarupg">
              <Itarupg />
          </Route>
          <Route path="/Kurisupg">
              <Kurisupg />
          </Route>
          <Route path="/Moekapg">
              <Moekapg />
          </Route>
          <Route path="/Lukapg">
              <Lukapg />
          </Route>
          <Route path="/Catpg">
              <Catpg />
          </Route>
          <Route path="/Suzuhapg">
              <Suzuhapg />
          </Route>
          <Route path="/Mahopg">
              <Mahopg />
          </Route>
          <Route path="/Kagaripg">
              <Kagaripg />
          </Route>
          <Route path="/Yukipg">
              <Yukipg />
          </Route>
        </Switch>

     </div>
    </div>
    </Router>
  )
}

ReactDOM.render(<App/>,document.getElementById("root"))
